*,*:after, *::before{
  box-sizing: border-box;
}

h1{
  text-align: center;
}

h1 a{
  text-decoration: none;
  color: #333;
}

nav a{
  text-decoration: none;
  color: 333;
}

nav{
  width: 60%;
  margin: 0 auto;
  display: flex;
  height: 50px;
  align-items: center;
  text-align: center;
}

nav a{
  width: 25%;
  display: inline-block;
  background: #131313;
  color: #fff;
  line-height: 50px;
}

p{
  text-align: center;
}